@use 'styles/const';
@use 'styles/layout';
@use 'styles/color';

$illustrateW: 30vw;

.root {
  @include layout.flexColumnMin100vh;
  padding-top: const.$navH;
  padding-bottom: const.$navH;

  .illustrate {
    max-width: $illustrateW;
    max-height: $illustrateW;
    margin: 0.5rem auto;
  }

  .header {
    @include layout.flexCol;
    h2 {
      text-align: center;
    }
  }

  .section {
    width: min(100%, 960px);
    padding: 1rem;
    h3 {
      color: color.$black;
      font-size: 20px;
      font-weight: bold;
      font-style: italic;
      margin-top: 1rem;
      margin-bottom: 1rem;
      padding-bottom: 1rem;
      border-bottom: 1px solid #aaaaaa50;
      text-align: end;
    }
  }

  .projects {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
  }

  .card {
    min-height: 150px;
    border-radius: 8px;
    background-color: #aaaaaa50;
    position: relative;
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    .label {
      background-color: #00000075;
      position: absolute;
      bottom: 0; left:0; right:0;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;
      padding:4px 8px;
      color: whitesmoke;
      h4 {
        font-weight: bold;
        margin: 0.6rem 0;
      }
      .icons {
        .icon {
          color: LightSkyBlue;
          font-size: 1.5rem;
        }
      }
    }
  }
}