@use 'styles/const';
@use 'styles/layout';
@use 'styles/color';

$illustrateW: 80vw;

.root {
  @include layout.flexColumnMin100vh;
  padding-top: const.$navH * 2;
  padding-bottom: const.$navH;
  background-color: ghostwhite;

  .illustrate {
    max-width: $illustrateW;
    max-height: $illustrateW;
    margin: 0.5rem auto;
    flex: 1;
  }

  .header {
    @include layout.flexCol;
    h2 {
      text-align: center;
    }
  }

  .ltor {
    display: flex;
    align-items: center;
    padding: 1rem;
    p {
      flex: 1;
    }
  }

  .topdown {
    @include layout.flexCol;
    padding: 1rem;
    p {
      text-align: center;
      margin-top: 1rem;
    }
  }
}