@use 'styles/const';

nav {
	color: white;
	background-color: #00000075;
	position: fixed;
	height: const.$navH;
	width: 100%;
	z-index: 10;
	padding-left: .5rem;
	transition: height .5s ease;
	overflow: hidden;

	&.menuOpen {
		height: const.$navH * 4;
	}

	& > div {
		display: flex; align-items: center; height: const.$navH;
	}

}

.footer {
	height: const.$navH;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0.5rem;
	color: #080808;

	& > :first-child {
		display: flex;
		align-items: center;
	}
	
	.copyleft {
		font-size: 1.2rem;
		margin-bottom: 2px;
		margin-right: 2px;
	}
	
	.icon {
		font-size: 2rem;
		margin: auto .2rem;
	}
}
